import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../dependencies/empresaMLFDependency';
import { CLASS_NAME, Selic } from '../../../../models/dtos/mlf/selic';
import { MES_ENUM_VALUES } from '../../../../models/enumerated/mlf/mesEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'ano',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const SelicSearchView: FC<SearchViewProps<Selic>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'ano',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Ano',
    },
    enumColumn(
      {
        dataField: 'mesInt',
        editable: false,
        filter: {
          type: FilterType.SELECT,
        },
        sort: true,
        text: 'Mês',
      },
      MES_ENUM_VALUES
    ),
    {
      dataField: 'taxa',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Taxa',
    },
  ];

  return (
    <WindowComponent
      title="Manutenção de Selic"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
      wikiId={1009}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'SelicEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default SelicSearchView;
